@charset "UTF-8";

.todo_list_template {
  width: 100%;
  max-width: 1024px;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  overflow: hidden; /* 자식 요소가 둥근 모서리를 넘치지 않도록 */
  margin: 0 auto;
}

@media (max-width: 550px) {
  .todo_list_template {
    width: 90%;
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
}

.title {
  padding: 2rem;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  background: linear-gradient(to right, #667eea, #764ba2);
  color: #fff;
  letter-spacing: 1px;
}

.form-wrapper {
  padding: 1.5rem;
  border-bottom: 1px solid #e9ecef;
  background: #fafafa;
}

.todos-wrapper {
  min-height: 200px;
  padding: 1rem 0;
  background: #fff;
}
