@charset "UTF-8";

.todo_item {
  padding: 1rem 1.5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.2s;
  margin: 0.5rem 1rem;
  border-radius: 8px;
}

.todo_item:hover {
  background-color: #f8f9fa;
  transform: translateX(5px);
}

/* todo-item 에 마우스가 있을때만 .remove 보이기 */
.todo_item:hover .remove {
  opacity: 1;
}

.remove {
  opacity: 0;
  font-weight: bold;
  color: #ff6b6b;
  margin-right: 1rem;
  font-size: 1.2rem;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: 50%;
}

.remove:hover {
  background-color: #ffe3e3;
  color: #c92a2a;
}

.todo_text {
  flex: 1;
  word-break: break-all;
  font-size: 1.1rem;
  color: #495057;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.checked {
  text-decoration: line-through;
  color: #adb5bd;
}

.check_mark {
  font-size: 1.2rem;
  color: #667eea;
  font-weight: bold;
  margin-left: 1rem;
}
