@charset "UTF-8";

/* 입력폼 서식 */
.form {
  max-width: 1024px;
  width: 100%;
  padding: 1.5rem;
  display: flex;
  gap: 10px; /* 입력창과 버튼 사이 간격 */
}

.form input {
  flex: 1;
  font-size: 1.1rem;
  outline: none;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  padding: 0.8rem 1rem;
  transition: all 0.2s ease;
  color: #495057;
}

.form input::placeholder {
  color: #adb5bd;
}

.form input:focus {
  border-color: #764ba2;
  box-shadow: 0 0 0 3px rgba(118, 75, 162, 0.1);
}

.create_btn {
  padding: 0 1.5rem;
  background: #667eea;
  border-radius: 8px;
  color: white;
  font-weight: 700;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.create_btn:hover {
  background: #764ba2;
  transform: translateY(-1px);
}

.create_btn:active {
  transform: translateY(1px);
}
